* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    color: #ffffff;
    text-decoration: none;
}

img {
    width: 100%;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background-color: #222222;
}

::-webkit-scrollbar-thumb {
    background: #d98e64;
    border-radius: 5px;
}

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #ffffff;
    background: #30363a;
}

code {
    font-family: 'Montserrat', sans-serif;
}

.container {
    margin: 0 auto;
}

@media (min-width: 576px) {
    .container {
        width: 540px;
        max-width: 100%;
    }
}

@media (min-width: 768px) {
    .container {
        width: 720px;
        max-width: 100%;
    }
}

@media (min-width: 992px) {
    .container {
        width: 960px;
        max-width: 100%;
    }
}

@media (min-width: 1280px) {
    .container {
        width: 1200px;
        max-width: 100%;
    }
}

.title {
    text-align: center;
    font-size: 3rem;
}

form {
    display: flex;
    flex-direction: column;
}

input {
    font-family: 'Montserrat';
    background: #222;
    height: 50px;
    border: none;
    padding-left: 20px;
    border-radius: 10px;
    outline: none;
    color: #ffffff;
    margin-bottom: 10px;
}

textarea {
    font-family: 'Montserrat';
    background: #222;
    outline: none;
    padding: 20px;
    border: none;
    border-radius: 10px;
    color: #ffffff;
    resize: none;
}

.btn {
    font-family: 'Montserrat';
    box-sizing: border-box;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    color: #ffffff;
    font-size: 1.1rem;
    border-radius: 10px;
    font-weight: 600;
    background: #d98e64;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all .3s;
}

.btn:hover {
    background: #ffffff;
    color: #000000;
}

/****************************************************************/

.video_bg {
    height: 100vh;
    position: fixed !important;
    top: 0;
    left: 0;
    width: 100vw;
    display: block;
    z-index: -1;
}

.video_bg:after {
    content: '';
    width: 100%;
    height: 100vh;
    top: 0;
    position: fixed;
    left: 0;
    /*background: rgba(48, 54, 58, 0.85);*/
    background: rgba(0, 0, 0, 0.8);
}

video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content {
    margin: 0 auto;
}

/****************************************************************/

.header {
    padding: 15px 3rem;
    background: rgba(48, 54, 58, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    backdrop-filter: blur(10px);
    z-index: 5;
}

.brand-logo {
    font-size: 2rem;
    display: flex;
    align-items: center;
}

.brand-logo img {
    margin-right: 20px;
}

.nav-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-wrapper ul {
    list-style: none;
    display: flex;
}

.main-menu li {
    margin-left: 2rem;
    font-size: 1.2rem;
}

.main-menu li a {
    transition: all .3s;
}

.main-menu li a:hover {
    color: #d98e64 !important;
}

/****************************************************************/

.banner {
    padding-top: 150px;
    height: 100vh;
    position: relative;
}

.banner .container {
    position: relative;
    height: 100%;
}

.main-title {
    text-align: center;
}

.main-title h2 {
    margin-top: 10px;
    font-weight: 500;
}

/****************************************************************/

.banner_slider {
    position: absolute !important;
    text-align: center;
    width: 100%;
    bottom: 10vh;
}

.banner_slider .swiper {
    width: 60%;
}

.banner_slider .swiper-slide {
}

.banner_slider .swiper-slide img {
    /*width: 100px;
    height: 100px;*/
    border: 15px solid rgba(48, 54, 58, 0.8);
    border-radius: 50%;
}


/****************************************************************/

.main-form {
    max-width: 550px;
    margin: 50px auto 0;
    padding: 30px;
    background-color: #30363a;
    color: #fff;
    border-radius: 10px;
    position: relative;
}

.exchange-form {

}

.exchange-form .swap-coins-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: #222222;
    border-radius: 50%;
    position: absolute;
    top: 70px;
    right: 10px;
    z-index: 1;
    font-size: 24px;
    cursor: pointer;
}

.exchange-form .exchange-form h2 {
    text-align: center;
    margin-bottom: 20px;
}

.exchange-form .form-group {
    margin-bottom: 10px;
    display: flex;
    position: relative;
    justify-content: space-between;
}

.exchange-form .form-group .error {
    position: absolute;
    pointer-events: none;
    left: 15px;
    top: -10px;
    background: darkred;
    padding: 3px 10px;
    border-radius: 10px;
    font-size: 0.9rem;
}

.exchange-form input[type="text"],
.exchange-form select {
    width: 100%;
    padding: 20px;
    height: 50px;
    border: 1px solid #555;
    /*border-radius: 5px;*/
    background-color: #555;
    color: #fff;
    outline: none;
    font-size: 1.2rem;
}

.exchange-form input[type="text"]::placeholder {
    color: #ffffff;
}

.exchange-form .form-group > input[type="text"] {
    width: 70%;
    border-radius: 5px 0 0 5px;
    text-align: right;
}

.exchange-form .select-wrapper {
    width: 29%;
}

.exchange-form .select-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 10px;
    color: #fff;
    border-radius: 0 5px 5px 0;
    height: 50px;
    background: url("assets/ico_select.png") 85% 50% no-repeat #555;
}

.exchange-form .select-header.open {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.exchange-form .select-arrow {
    width: 8px;
    height: 8px;
    border-left: 2px solid #fff;
    border-bottom: 2px solid #fff;
    transform: rotate(-45deg);
    margin-left: 8px;
    transition: transform 0.3s ease;
}

.exchange-form .select-arrow.open {
    transform: rotate(135deg);
}

.exchange-form .select-options {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: #555;
    color: #fff;
    /*border: 1px solid #555;*/
    border-top: none;
    max-height: 200px;
    overflow-y: auto;
    z-index: 1;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.exchange-form .select-options input[type="text"] {
    width: 100%;
    padding: 10px;
    border: none;
    outline: none;
    background-color: #333;
    color: #fff;
    border-radius: 0;
}

.exchange-form .select-option {
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
}

.exchange-form .select-option img {
    width: 32px;
    height: 32px;
    margin-right: 15px;
}

.exchange-form .select-option:hover {
    background-color: #666;
}

.exchange-form button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #d98e64;
    color: #fff;
    border: 1px solid transparent;
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s;
    height: 50px;
    font-size: 1.1rem;
}

.exchange-form button:hover {
    background-color: transparent;
    border-color:  #d98e64;
}

/****************************************************************/

.exchange-info {}


.swap-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 30px;
    margin-bottom: 40px;
    position: relative;
}

.swap-items .edit-coins-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: #222222;
    border-radius: 50%;
    position: absolute;
    top: 5px;
    right: -20px;
    z-index: 1;
    font-size: 18px;
    cursor: pointer;
    padding: 0;
    border: none;
}

.swap-items > div {
    height: 50px;
    padding: 0 20px;
    display: flex;
    align-items: center;
    border: 1px solid #d98e64;
    border-radius: 5px;
    width: 43%;
    position: relative;
}

.swap-items > div span {
    display: block;
    position: absolute;
    top: -10px;
    left: 10px;
    padding: 0 10px;
    background: #30363a;
}

.swap-items > div b {
    font-size: 1.1rem;
}

.exchange-info .form-group {
    margin-bottom: 30px;
    position: relative;
}

.exchange-info .form-group > span {
    display: block;
    margin-bottom: 10px;
}

.exchange-info .form-group .error {
    position: absolute;
    pointer-events: none;
    right: 15px;
    bottom: -15px;
    background: darkred;
    padding: 3px 10px;
    border-radius: 10px;
}

.exchange-info input {
    width: 100%;
    margin-bottom: 0;
}

.exchange-info .checkbox-wrap {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.exchange-info .checkbox-wrap input[type='checkbox'] {
    /* removing default appearance */
    -webkit-appearance: none;
    appearance: none;
    /* creating a custom design */
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    margin-bottom: 0;
    border-radius: 2px;
    margin-right: 10px;
    border: 1px solid #d98e64;
    outline: none;
    cursor: pointer;
    position: relative;
}

.exchange-info .checkbox-wrap input[type='checkbox']:before {
    content: url("assets/ckeck.svg");
    position: absolute;
    top: 1px;
    left: 1px;
    opacity: 0;
    transition: all .3s;
}

.exchange-info .checkbox-wrap input[type='checkbox']:checked:before {
    opacity: 1;
}

.exchange-info .checkbox-wrap a {
    text-decoration: underline;
}

.exchange-info .checkbox-wrap label {
    cursor: pointer;
}

.exchange-info button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #d98e64;
    color: #fff;
    border: 1px solid transparent;
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s;
    height: 50px;
    font-size: 1.1rem;
}

.exchange-info button.disabled {
    opacity: .5;
    pointer-events: none;
}

.exchange-info button:hover {
    background-color: transparent;
    border-color:  #d98e64;
}

